import React, { Component } from 'react';
import { connect } from 'react-redux';

import { socialMediaInfo, pickAPhoto } from '../../data/Data';

import './CardProfile.scss';

class CardProfile extends Component {

  state = {
    photo: Math.floor((Math.random() * 5) + 1)
  };

  constructor(props) {
    super(props);
    setInterval(() => {
      this.setState({ photo: Math.floor((Math.random() * 5) + 1) })
    }, 60000);
  }

  componentDidMount(){
    this.props.setRef(this);
  }

  componentWillUnmount(){
    this.props.setRef(undefined);
  }

  setAllSocialMedia = () => {
    let socialMedia = []
    socialMedia = socialMediaInfo.map((data, index) => {
      return (
        <a key={index} href={data.link} target="_blank" rel="noopener noreferrer">
          <svg className="iconsStyle">
            {data.photo}
          </svg>
        </a>)
    });
    return socialMedia;
  }

  setAllInfo2 = (n) => {
    let key = ''
    let arr = []
    for (key in this.props.basicInfo2) {
      arr.push(<div className="basicInfoStyle" key={key}>{this.props.basicInfo2[key][n]}</div>);
    }
    return arr;
  }

  render() {
    return (
      <div className="cardProfile">
        <section className="photoStyle">
          <img src={pickAPhoto(this.state.photo)} alt="villa" className="imgStyle" />
        </section>
        <section className="infoStyle">
          <div className="nameStyle">
            {this.props.title}
          </div>
          <div className="positionStyle">
            {this.props.basicInfo.position}
          </div>
          <hr className="hrStyle" />
          <section className="infoContainer">
            <div className="titlesStyle">
              {this.setAllInfo2(0)}
            </div>
            <div className="dataStyle">
              {this.setAllInfo2(1)}
            </div>
          </section>
        </section>
        <section className="mediaStyle">
          {this.setAllSocialMedia()}
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => ({
    basicInfo: state.DataReducer.basicInfo,
    basicInfo2: state.DataReducer.basicInfo2,
    title: state.DataReducer.title
})

export default connect(mapStateToProps,null)(CardProfile);
