import React, { Component } from 'react';
import { connect } from 'react-redux';

import './Hobbies.scss';

class Hobbies extends Component {

  state = {
    onHover: 0
  };

  setAllHobbies = () => {
    let hobbies = []
    hobbies = this.props.hobbiesInfo.map((data, index) => {
      switch (data.id) {
        case 'travel':
          return <img key={index} className={(this.state.onHover !== index) ? "hobbiesImgStyle" : "hobbiesAirplaneStyleMove hobbiesImgStyle"} onMouseOver={() => this.setState({ onHover: index })} src={data.photo} alt={data.name}></img>
        case 'sport':
          return <img key={index} className={(this.state.onHover !== index) ? "hobbiesImgStyle" : "hobbiesSportStyleMove hobbiesImgStyle"} onMouseOver={() => this.setState({ onHover: index })} src={data.photo} alt={data.name}></img>
        case 'games':
          return <img key={index} className={(this.state.onHover !== index) ? "hobbiesImgStyle" : "hobbiesGameStyleMove hobbiesImgStyle"} onMouseOver={() => this.setState({ onHover: index })} src={data.photo} alt={data.name}></img>
        default:
          return <img key={index} className={(this.state.onHover !== index) ? "hobbiesImgStyle" : "hobbiesImgStyleMove hobbiesImgStyle"} onMouseOver={() => this.setState({ onHover: index })} src={data.photo} alt={data.name}></img>
      }
    });
    return hobbies;
  }

  render() {
    return (
      <div className="hobbiesContainer">
        <section className="hobTitleContStyle">
          <p className="hobTitleStyle">{this.props.hobbiesInfo[this.state.onHover]['name']}</p>
        </section>
        <section className="hobbiesContStyle">
          {this.setAllHobbies()}
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  hobbiesInfo: state.DataReducer.hobbiesInfo
})

export default connect(mapStateToProps,null)(Hobbies);
