import React, { Component } from 'react';
import { connect } from 'react-redux';

import { socialMediaInfo, pickAPhoto } from '../../data/Data';

import './Contact.scss';

class Contact extends Component {

    state = {
        photo: 2
    };

    constructor(props) {
        super(props);
        setInterval(() => {
            this.setState({ photo: Math.floor((Math.random() * 6) + 1) })
        }, 60000);
    }

    setAllSocialMedia = () => {
        let socialMedia = []
        socialMedia = socialMediaInfo.map((data, index) => {
            return (
                <a key={index} href={data.link} target="_blank" rel="noopener noreferrer">
                    <svg className="contactStyleMedia">
                        {data.photo}
                    </svg>
                </a>)
        })
        return socialMedia;
    }

    render() {
        let year = new Date();
        year = year.getFullYear();
        return (
            <div className="contactContainer">
                <section className="textConteiner">
                    <div className="titleContact">{this.props.getInTouch[0]}</div>
                    <div className="textContact">{this.props.getInTouch[1]}</div>
                </section>
                <section className="photoLegend">
                    <div className="imgContactContainer">
                        <img src={pickAPhoto(this.state.photo)} alt="mavg" className="imgContactStyle" />
                    </div>
                    <div className="rightsContainer">
                        ¬© {year} Alejandro Villa. {this.props.rights}.
                    </div>
                </section>
                <section className="mediaContainer">
                    {this.setAllSocialMedia()}
                </section>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    rights: state.DataReducer.rights,
    getInTouch: state.DataReducer.getInTouch
})

export default connect(mapStateToProps, null)(Contact);