import React, { Component } from 'react';

import { socialMediaInfo } from '../../data/Data';

import './SocialMedia.scss';

let lastScrollY = 0;
let scrollLimit = 2100;
let sizeOfIcons = 50;

class SocialMedia extends Component {

  state = {
    appear: false,
    windowHeight: 0,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    this.setState({windowHeight:document.body.scrollHeight - document.body.clientHeight});
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    lastScrollY = window.scrollY;
    if ((this.props.cardBounds <= lastScrollY && scrollLimit >= lastScrollY) && !this.state.appear) {
      this.setState({ appear: true });
    } else if (this.props.cardBounds >= lastScrollY && this.state.appear) {
      this.setState({ appear: false });
    }
    if ((this.state.windowHeight - sizeOfIcons) <= lastScrollY && this.state.appear) {
      this.setState({ appear: false });
    }
  }

  setAllSocialMedia = () => {
    let socialMedia = [];
    socialMedia = socialMediaInfo.map((data, index) => {
      return (
        <a className={(this.state.appear) ? "anchorStyle" : "anchorStyleNone"} key={index} href={data.link} target="_blank" rel="noopener noreferrer">
          <svg className="iconsStyleMedia">
            {data.photo}
          </svg>
        </a>)
    });
    return socialMedia;
  }

  render() {
    return (
      <div className="staticContent">
        {this.setAllSocialMedia()}
      </div>
    );
  }
}

export default SocialMedia;
