import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';

import './Home.scss';

import CardProfile from '../components/cardProfile/CardProfile';
import SocialMedia from '../components/socialMedia/SocialMedia';
import Hobbies from '../components/hobbies/Hobbies';
import Contact from '../components/contact/Contact';

import bgPhoto from '../assets/background.jpg';
import VillaSimpson from '../assets/VillaSimpson.png';
import alexResume from '../assets/Alejandro Villa Resume.pdf';
import { skills } from '../data/Data';

class Home extends Component {

  state = {
    cardBounds: 0
  };

  getSkills = () => {
    let skillArr = skills.map((skill, index) => {
      return (
        <div key={index} className="skillsStyle">
          {(skill.img !== '') ? <div className="imgBoxSize"><img className="imgSkillStyle" src={skill.img} alt={skill.name} /></div> : null}
          <p className="nameSkillStyle">{skill.name}</p>
        </div>
      )
    })
    return skillArr;
  }

  componentDidMount() {
    let boundsCP = ReactDOM
      .findDOMNode(this.child)
      .getBoundingClientRect();

    this.setState({ cardBounds: (boundsCP.height + 48) });
  }

  render() {
    return (
      <div className="homeStyle" ref="HomeView">
        <img className="bgPhotoStyle" src={bgPhoto} alt="bgPhoto"/>
        <CardProfile className="positionCardProf" setRef={ref => (this.child = ref)}></CardProfile>
        <div className="moreStyle">

          <a className="downloadStyle" href={alexResume} target="_blank" rel="noopener noreferrer">
            {this.props.buttonTxt}
          </a>
          <div className="recentWorkStyle">
            {this.props.recentWork}
          </div>

          <hr className="hrStyleHome"/>

          <div className="skillsTitleStyle">
            {this.props.skillsTitle}
          </div>
          <div className="skillsContainer">
            {this.getSkills()}
          </div>

          <hr className="hrStyleHome"/>
          <div className="hobbiesTitleStyle">
            {this.props.hobbiesTitle}
            <img className="villaSimpson" src={VillaSimpson} alt="simpson"/>
          </div>
          <Hobbies></Hobbies>
          <Contact></Contact>
          <SocialMedia cardBounds={this.state.cardBounds}></SocialMedia>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  recentWork: state.DataReducer.recentWork,
  buttonTxt: state.DataReducer.buttonTxt,
  hobbiesTitle: state.DataReducer.hobbiesTitle,
  skillsTitle: state.DataReducer.skillsTitle,
})

export default connect(mapStateToProps,null)(Home);
